/* src/components/DropCountdown.css */
/* Reset and base styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #ffffff;
  }
  
  /* Container styles */
  .container {
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 20px;
  }

  
  /* Main content */
  .main-content {
    padding: 40px 0;
  }
  
  .drop-title {
    font-size: 36px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 32px;
    letter-spacing: 1px;
  }
  
  /* Product image */
  .product-image-container {
    width: 100%;
    max-width: 600px;
    margin: 0 auto 48px;
    /* 넘치는 부분을 숨기기 위해 추가 */
    overflow: hidden;
    /* 호버 시 약간의 그림자 효과를 위한 transition */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .product-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    /* 기존 box-shadow 제거하고 transition 추가 */
    transition: transform 0.3s ease;
    cursor: pointer;
  }
  
  /* 호버 효과 */
  .product-image:hover {
    transform: scale(1.05); /* 5% 크기 증가 */
  }
  
  /* Countdown timer */
  .countdown-container {
    display: flex;
    justify-content: center;
    gap: 32px;
    margin-bottom: 32px;
  }
  
  .countdown-item {
    text-align: center;
  }
  
  .countdown-number {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 8px;
  }
  
  .countdown-label {
    font-size: 12px;
    text-transform: uppercase;
    color: #666666;
    letter-spacing: 1px;
  }
  
  .countdown-separator {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 24px;
  }
  
  /* Release date */
  .release-date {
    text-align: center;
    font-size: 14px;
    color: #666666;
    margin-bottom: 32px;
  }
  
  /* Pre-order button */
  .pre-order-button {
    display: block;
    width: 200px;
    height: 48px;
    margin: 0 auto;
    background-color: #000000;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  .pre-order-button:hover {
    background-color: #333333;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .countdown-container {
      gap: 16px;
    }
  
    .countdown-number {
      font-size: 28px;
    }
  
    .countdown-label {
      font-size: 10px;
    }
  
    .drop-title {
      font-size: 28px;
    }
  }
  
  @media (max-width: 480px) {
    .countdown-container {
      gap: 8px;
    }
  
    .countdown-number {
      font-size: 24px;
    }
  
    .countdown-label {
      font-size: 9px;
    }
  
    .drop-title {
      font-size: 24px;
    }
  
    .pre-order-button {
      width: 100%;
      max-width: 200px;
    }
  }