/* Signup.css */
.signup-container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
  }
  
  .signup-box {
    width: 380px;
    padding: 24px;
  }
  
  .signup-title {
    font-size: 24px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 32px;
  }
  
  .input-group {
    margin-bottom: 16px;
  }
  
  .email-group {
    display: flex;
    gap: 8px;
  }
  
  .input-field {
    flex: 1;
    padding: 8px 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    width: 100%;
  }
  
  .input-field:disabled {
    background-color: #f5f5f5;
  }
  
  .verify-button {
    padding: 8px 16px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .verify-button:hover {
    background-color: #333;
  }
  
  .signup-button {
    width: 100%;
    padding: 8px 16px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .signup-button:hover {
    background-color: #333;
  }